
.sign {padding: 30px 20px; background: url(../assets/images/live_player_bg.jpg); background-size: cover; text-align: left; border-bottom: solid 15px #f7f7f7;
}
.sign img {width: 60px;
}
.sign span {color: #fff; text-align: center; padding: 10px 40px; border: solid 1px #fff; border-radius: 5px; float: right; margin-top: 10px;
}




