
.group-item-con {padding-left: 70px; margin-bottom: 40px; position: relative;
}
.group-item-con .smallPic {width: 60px; position: absolute; left: 0; top: 0; border-radius: 5px;
}
.group-item-con .text {width: 65%; text-align: left;
}
.group-item-con .text h4 {width: 100%; color: #444;
}
.group-item-con .text p {width: 100%; word-break: break-word; color: #999; font-size: 12px;
}
.group-item-con .ok {position: absolute; top: 10px; right: 0;
}
.group-item-con .ok span {margin-right: 10px; font-size: 12px; color: #d3d3d3;
}
.group-item-con .ok img {width: 25px; float: right;
}



