
.remind {padding: 20px 0 20px 20px; border-bottom: solid 15px #f7f7f7;
}
.remind-title {padding: 20px 20px 20px 0; text-align: left; border-bottom: solid 1px #e5e5e5;
}
.remind-title img {display: inline-block; width: 30px; vertical-align: middle;
}
.remind-title span {display: inline-block; height: 25px; line-height: 25px; font-size: 18px; vertical-align: middle;
}
.remind-title .arrow {float: right; width: 25px;
}
.remind-content {padding-top: 20px;
}
.remind-content p {font-size: 14px; color: #e5e5e5;
}



