
.hot {padding: 20px 15px; border-bottom: solid 15px #f7f7f7;
}
.hot h3 {text-align: left; font-size: 16px; font-weight: normal; margin-bottom: 20px;
}
.hot h3 span {float: right;
}
.hot h3 span a {text-decoration: none; color: #60da85; font-size: 14px;
}
.audioPic {height: 180px; overflow: hidden; position: relative;
}



