
.search {position: absolute; left: 10px; top: 6px; width: 85%;
}
.search input {height: 35px; background-color: #fff; line-height: 35px; text-indent: 3em; border-radius: 5px; width: 100%; outline: none; border: none;
}
.search img {width: 25px; position: absolute; top: 5px;
}
.search .leftImg {left: 5px;
}
.search .rightImg {right: 10px;
}



